<template>
  <div>
    <h1>Задачи на сегодня</h1>
    <hr>
    <button><router-link to="/">Сменить пользователя</router-link></button>
    <AddTodo
      @add-todo="addTodo"
    />
    <select v-model="filter">
      <option value="all">Все</option>
      <option value="completed">Выполненые</option>
      <option value="not-completed">Не выполненые</option>
    </select>
    <TodoList 
      :todos="filteredTodos"
      @remove-todo="removeTodo"
    />
    <router-view/>
  </div>
</template>
<script>
import TodoList from '@/components/TodoList.vue'
import AddTodo from '@/components/AddTodo.vue'
export default {
  props: ['name'],
  data() {
    return {
      filter: 'all',
      todos: [
        {id: 1, title: 'Покушать', completed: false},
        {id: 2, title: 'Помыть посуду', completed: false},
        {id: 3, title: 'Лечь спать', completed: false},
      ],
      message: '', 
    }
  },
  computed: {
    filteredTodos() {
      if (this.filter === 'all') {
        return this.todos
      }
      if (this.filter === 'completed') {
        return this.todos.filter(t => t.completed)
      }
      if (this.filter === 'not-completed') {
        return this.todos.filter(t => !t.completed)
      }
    }
  },
  methods: {
    removeTodo(id) {
      this.todos = this.todos.filter(t => t.id !== id)
    },
    addTodo(todo) {
      this.todos.push(todo)
      this.message = "Hello";
    },
  },
  mounted(){
    console.log('App Mounted');
      if (localStorage.getItem('todos'))
        this.todos = JSON.parse(localStorage.getItem('todos'));
  },

  watch: {
    todos: {
      handler() {
        console.log('todo_items');
        localStorage.setItem('todos', JSON.stringify(this.todos));
      },
      deep: true,
    }
  },

  components: {
    TodoList, AddTodo,
  },
};
</script>